<template>
<!-- 版本选择 -->
  <div class="version-election">
    <div class="title">
      版本选择
    </div>
      <!-- <van-dropdown-menu :overlay="false" get-container="body" > -->
      <van-dropdown-menu get-container="body" >
        <van-dropdown-item v-model="value" :title='title' :options="option" @change='handelChange'/>
      </van-dropdown-menu>
    <van-button round type="info" block @click="getIntoSystem" :disabled="buttonDisabled">进入系统</van-button>
  </div>
</template>

<script>
export default {
  name: 'VersionSelection',
  data () {
    return {
      value: '',
      title: '请选择版本',
      option: [],
      buttonDisabled: true
    }
  },
  async created () {
    const res = await this.$getHttp(`${this.$baseUrl}/OS-PM/version/getAllVersion`, {})
    const optionArray = []
    res.data.forEach(e => {
      const obj = {
        text: e.versionName,
        value: e.id
      }
      optionArray.push(obj)
    })
    this.option = optionArray
  },
  methods: {
    handelChange (val) {
      this.option.forEach(obj => {
        if (obj.value === val) {
          this.title = obj.text
          this.$store.commit('changeVersionId', val);
          // window.localStorage.setItem('versionId', val)
        }
      })
      
      this.buttonDisabled = false
    },
    getIntoSystem () {
      const versionMsg = {
        title: this.title,
        value: this.value,
        option: this.option
      }
      window.localStorage.setItem('versionMsg', JSON.stringify(versionMsg))
      this.$router.push({ name: 'Home', query: { title: this.title, value: this.value, option: this.option } })
    }
  }
}
</script>

<style lang='less' scoped>
  .version-election {
    text-align: center;
    margin-top: 4%;
    .title {
      height: 2rem;
      line-height: 2rem;
      font-weight: 600;
      font-size: 1.375rem;
    }
    .van-button {
      width: 90%;
      display: inline-block;
      // text-align: center;
    }
    ::v-deep .van-dropdown-menu {
      width: 90%;
      display: inline-block;
      border-radius: 1rem;
      margin: 6rem 0rem;
      .van-dropdown-menu__bar {
        border: 1px solid #808187;
        border-radius: 0.5rem;
      }
      .van-dropdown-menu__title::after {
        right: -6.25rem
      }
    }
  }
</style>
